import React from "react";
import ServiceItem from "./ServiceItem";
import { useTranslation } from "react-i18next";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { collectionThree } from "../../../components/options";

export default function Services({ services }) {
	const { t } = useTranslation();

	return (
		<div className="services">
			<h3>{t("our-services")}</h3>
			<div className="row services-wrapper">
				{services?.dynamicCollectionBlock
					?.slice(0, 2)
					.map((item, i) => (
						<ServiceItem
							key={i}
							index={i}
							link={item.photos[0].link}
							image={item.photos[0]}
							column={`col-lg-6`}
							title={item.name}
						/>
					))}
			</div>
			<div className="row services-wrapper">
				{services?.dynamicCollectionBlock?.slice(2).map((item, i) => (
					<ServiceItem
						key={i}
						index={i}
						link={item.photos[0].link}
						image={item.photos[0]}
						column={`col-lg-4`}
						title={item.name}
					/>
				))}
			</div>
			<div className="services-wrapper-mobile only-mobile">
				<Splide options={collectionThree}>
					{services?.dynamicCollectionBlock?.map((item, i) => (
						<SplideSlide key={i}>
							<ServiceItem
								link={item.photos[0].link}
								image={item.photos[0]}
								index={i}
								column={"col-lg-12"}
								title={item.name}
							/>
						</SplideSlide>
					))}
				</Splide>
			</div>
		</div>
	);
}
