import React from "react";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { dt } from "../../../../i18n";
import { miniSliderTwo } from "../../../../components/options";
import ShimmerProduct from "../../../../components/features/loaders/shimmer";
import { Link } from "react-router-dom";
import MediaImage from "../../../../components/features/product/MediaImage";

function MiniSliderTwo({ purchases }) {
	return purchases?.products ? (
		<div className="mini-slider">
			<h2>{dt(purchases?.name)}</h2>
			<Splide className="splide mini-slider" options={miniSliderTwo}>
				{purchases.products?.map((item, i) => {
					let image = item?.variation?.images[0];
					return (
						<SplideSlide key={i}>
							<Link to={`/product/${item?.variation?.id}`}>
								<div className="cat-block">
									<div>
										<MediaImage
											image={image}
											largeSize={"150x_"}
											mediumSize={"150x_"}
											smallSize={"150x_"}
											alt={item.variation.name}
										/>
									</div>
									<h3 className="cat-block-title">
										{dt(item?.variation?.name)}
									</h3>
								</div>
							</Link>
						</SplideSlide>
					);
				})}
			</Splide>
		</div>
	) : (
		<ShimmerProduct />
	);
}

export default MiniSliderTwo;
