import React, { useEffect, useMemo, useState } from "react";
import { Helmet } from "react-helmet";
import { useSelector } from "react-redux";
import Collections from "../components/collections/Collections";
import BlockWithSupplier from "../components/blocks/BlockWithSupplier";
import BlockBannerCollection from "../components/blocks/BlockBannerCollection";
import Services from "../components/Services";
import MiniSliderTwo from "../components/banners/MiniSliderTwo";
import MenuSlideSection from "../components/MenuSlideSection";
import SuppliersListOne from "../components/suppliers/SuppliersListOne";
import { collectionVibo } from "../../../components/options";

function HomePageFour() {
	const { blocks } = useSelector(state => state.blocks);
	const marketData = useSelector(state => state.marketData);
	const [menuChild, setMenuChild] = useState(null);

	const head = useMemo(() => {
		return {
			title: marketData.name,
			description: marketData.settings && marketData.settings.description,
			keyword: marketData.settings && marketData.settings.keyword,
			favicon: marketData.favicon
		};
	}, []);

	useEffect(() => {
		window.addEventListener("resize", resizeHandle);
		resizeHandle();

		return () => {
			window.removeEventListener("resize", resizeHandle);
		};
	}, []);

	function resizeHandle() {
		if (window.document.querySelector("body")?.offsetWidth < 1000)
			setMenuChild([]);
	}

	const withSupplierCollection = blocks?.filter(
		block => block.title === "with_supplier_collection"
	);
	const collectionsBlock = blocks?.filter(
		block => block.title === "collection"
	);
	const bigBlocks = blocks?.filter(block => block.title === "big_block");
	const services = blocks?.find(block => block.title === "service");
	const purchases = blocks?.find(block => block.title === "purchases");

	return (
		<>
			<Helmet>
				<title>{head.title}</title>
				<meta name="description" content={head.description} />
				<meta name="keywords" content={head.keyword} />
				<meta name="apple-mobile-web-app-title" content={head.title} />
				<meta name="application-name" content={head.title} />
				<script
					src="https://www.google.com/recaptcha/api.js"
					async
					defer
				></script>
			</Helmet>
			<h1 className="d-none">{head.title}</h1>

			<div className="home-page-four">
				<div className="container">
					<MenuSlideSection
						menuChild={menuChild}
						setMenuChild={setMenuChild}
					/>

					<div className="collections row mt-4">
						{collectionsBlock?.map(block => (
							<div
								key={block.id}
								className="col-lg-6 col-12 my-3"
							>
								<Collections
									option={collectionVibo}
									block={block}
									id={block.id}
									title={block.name}
								/>
							</div>
						))}
					</div>

					{bigBlocks?.map((block, index) => (
						<BlockBannerCollection
							block={block}
							key={index}
							menuCol={"col-lg-3 col-12"}
							bannerCol={"col-md-3 col-0"}
							collectionCol={"col-lg-6 col-12"}
							collectionSize={"col-4 col-lg-4 col-md-4"}
							doubleCollection={false}
						/>
					))}

					<MiniSliderTwo purchases={purchases} />

					{withSupplierCollection?.map(block => (
						<BlockWithSupplier
							block={block}
							key={block.id}
							blockName={block.name}
							id={block.id}
						/>
					))}

					<SuppliersListOne />

					<Services services={services} />
				</div>
			</div>
		</>
	);
}

export default HomePageFour;
