import React, { useState } from "react";
import { useSelector } from "react-redux";
import Slider from "./banners/Slider";
import HomeMenu, { MenuChildList } from "./HomeMenu";

export default function MenuSlideSection({ menuChild, setMenuChild }) {
	const [menuAllActive, setMenuAllActive] = useState(false);
	const { menuVertical } = useSelector(state => state.menu);

	return (
		<div
			className="menu-slider row"
			onMouseLeave={() => {
				setMenuChild(null);
			}}
		>
			{menuVertical.length ? (
				<div className="col-lg-3 col-12 only-desktop">
					<HomeMenu
						setMenuAllActive={setMenuAllActive}
						setMenuChild={setMenuChild}
					/>
				</div>
			) : null}
			<div
				className={`${
					menuVertical.length ? `col-lg-9` : ``
				} col-12 position-relative`}
			>
				{menuChild && menuChild.length > 0 && (
					<MenuChildList
						menuAllActive={menuAllActive}
						setMenuAllActive={setMenuAllActive}
						menuChild={menuChild}
						seeMore={true}
						slice={11}
					/>
				)}
				<Slider />
			</div>
		</div>
	);
}
