import React from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import MediaImage from "../../../../components/features/product/MediaImage";

function SupplierItemOne({ supplier }) {
	const { t } = useTranslation();
	if (!supplier.products) {
		return "";
	}
	const product1 = supplier.products[0];
	const product2 = supplier.products[1];
	const product3 = supplier.products[2];

	return (
		<div className="supplier-wrapper">
			<div className="d-flex">
				<div className={`logo ${supplier.logo ? `` : `no-logo`}`}>
					<MediaImage
						image={supplier.logo}
						largeSize={"300x_"}
						mediumSize={"300x_"}
						smallSize={"300x_"}
						alt={supplier.name}
					/>
				</div>
				<div className="about-supplier">
					<Link to={`/supplier/${supplier.id}`}>
						<span className="title">{supplier.name}</span>
					</Link>
					<span className="sub-title">{t("market_products")}</span>
					<span className="rating">{t("five_rating")}</span>
				</div>
			</div>
			<Link to={`/supplier/${supplier.id}`} className="images">
				<div
					className={`big-img ${
						product1?.variation.images[0] ? `` : `no-product`
					}`}
				>
					<MediaImage
						image={product1?.variation.images[0]}
						largeSize={"300x_"}
						mediumSize={"300x_"}
						smallSize={"300x_"}
						alt={product1?.variation.name}
					/>
				</div>
				<div className="small-images d-flex justify-content-between flex-column ">
					<div
						className={`small-img ${
							product2?.variation.images[0] ? `` : `no-product`
						}`}
					>
						<MediaImage
							image={product2?.variation.images[0]}
							largeSize={"300x_"}
							mediumSize={"300x_"}
							smallSize={"300x_"}
							alt={product2?.variation.name}
						/>
					</div>
					<div
						className={`small-img ${
							product3?.variation.images[0] ? `` : `no-product`
						}`}
					>
						<MediaImage
							image={product3?.variation.images[0]}
							largeSize={"300x_"}
							mediumSize={"300x_"}
							smallSize={"300x_"}
							alt={product3?.variation.name}
						/>
					</div>
				</div>
			</Link>
		</div>
	);
}
export default SupplierItemOne;
