import { Splide, SplideSlide } from "@splidejs/react-splide";
import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { getSuppliersList } from "../../../../api/cache/main";
import { setSuppliers } from "../../../../actions/suppliers";
import { sliderSuppliers } from "../../../../components/options";
import SupplierItem from "./SupplierItemOne";
import QueryString from "qs";
import product from "./mockProduct.json";

function SuppliersListOne() {
	const { suppliers } = useSelector((state) => state.suppliers);

	const dispatch = useDispatch();
	const { t } = useTranslation();

	useEffect(() => {
		if (!suppliers) {
			getSuppliersList(QueryString.stringify({ page: 1, size: 10 })).then(
				(res) => {
					const suppliers = [];
					for (const i of res.items) {
						if (!i.products) {
							i.products = [product];
							suppliers.push(i);
						} else suppliers.push(i);
					}
					dispatch(setSuppliers(suppliers));
				}
			);
		}
	}, []);

	function toTop() {
		window.scroll({
			top: 0,
		});
	}

	return (
		<div className="suppliers">
			<div className="suppliers_header">
				<h3>{t("all-suppliers")}</h3>
				<Link to="/suppliers" onClick={toTop}>
					<span>{t("see-all")}</span>
					<i className="icon-angle-right d-none"></i>
				</Link>
			</div>
			{suppliers ? (
				<Splide options={sliderSuppliers}>
					{suppliers.map((item, i) => {
						return item.products?.length > 0 ? (
							<SplideSlide key={i}>
								<SupplierItem supplier={item} />
							</SplideSlide>
						) : null;
					})}
				</Splide>
			) : null}
		</div>
	);
}
export default SuppliersListOne;
