import React, { useEffect } from "react";
import ProductCard from "../../../../components/features/product";
import ShimmerProducts from "../../../../components/features/loaders/ShimmerProducts";
import { dt } from "../../../../i18n";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { setSuppliers } from "../../../../actions/suppliers";
import { getEverySupplier } from "../../../../api/supplierProducts";
import MediaImage from "../../../../components/features/product/MediaImage";
import { useSelector } from "react-redux";
import { dispatch } from "../../../../store";
import { Link } from "react-router-dom";

function BlockWithSupplier({ block, blockName }) {
	const banner = block.dynamicCollectionBlock[0].photos[0];
	const { suppliers } = useSelector(state => state.suppliers);

	let suppliersIds = block.products
		.reduce((pv, cv) => [...pv, cv.stock.supplier], [])
		.filter((el, i, a) => i === a.indexOf(el));

	async function getSuppliers() {
		suppliersIds.reduce((pv, cv) => {
			if (suppliers.find(s => s.id === cv)) {
				return [...pv.filter(p => p !== cv)];
			}
			return pv;
		}, []);

		suppliersIds = suppliersIds.filter(
			id => !suppliers.find(s => s.id === id) && id
		);
		if (suppliersIds.length) {
			const res = await getEverySupplier(suppliersIds);
			dispatch(setSuppliers([...suppliers, ...res.items]));
		}
	}

	useEffect(() => {
		if (suppliers) getSuppliers();
	}, [suppliers]);

	return (
		<div className="collection-with-supplier">
			<div className="row">
				<div className="col-lg-9 col-12">
					<h2>{dt(blockName)}</h2>
					{block.products ? (
						<Splide
							options={{
								rewind: true,
								perPage: 4,
								focus: "left",
								pagination: false,
								arrows: false,
								breakpoints: {
									991: {
										perPage: 4
									},
									768: {
										perPage: 3
									},
									560: {
										perPage: 2
									}
								}
							}}
						>
							{block.products.map((product, i) => {
								return (
									<SplideSlide
										key={i}
										className="product-item"
									>
										<ProductCard
											suppliers={suppliers}
											haveSupplier={true}
											product={product}
											type="sidebar"
											onAddToCart={() => {}}
										/>
									</SplideSlide>
								);
							})}
						</Splide>
					) : (
						<div className="w-100">
							<ShimmerProducts columns={4} />
						</div>
					)}
				</div>
				<div className="col-lg-3">
					<Link to={banner.link}>
						<MediaImage
							image={banner}
							largeSize="300x_"
							mediumSize="150x_"
							smallSize="50x_"
							alt="banner"
						/>
					</Link>
				</div>
			</div>
		</div>
	);
}

export default BlockWithSupplier;
