import React, { useEffect, useState } from "react";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import ShimmerProducts from "../../../../components/features/loaders/ShimmerProducts";
import ProductCard from "../../../../components/features/product";
import { collectionThree } from "../../../../components/options";
import { dt } from "../../../../i18n";

function Collections({ title, block, option = collectionThree }) {
	const [collectionsDouble, setCollections] = useState([]);

	useEffect(() => {
		if (block) {
			const toMatrix = (arr, width = 2) =>
				arr.reduce(
					(rows, key, index) =>
						(index % width === 0
							? rows.push([key])
							: rows[rows.length - 1].push(key)) && rows,
					[]
				);
			setCollections(toMatrix(block.products));
		}
	}, []);

	return (
		<div className="collection-list-wrapper">
			{block.products ? (
				<>
					{title ? <span className="title">{dt(title)}</span> : null}
					<div className="row collections-list collections-list-desktop">
						{collectionsDouble?.slice(0, 3).map((item, index) => (
							<div key={index} className="collection-item">
								{item.map((item, i) => (
									<ProductCard key={i} product={item} />
								))}
							</div>
						))}
					</div>
					<Splide
						className="splide collections-list collections-list-mobile"
						options={option}
					>
						{block.products.map((item, index) => (
							<SplideSlide
								key={index}
								className="collection-item"
							>
								<ProductCard product={item} />
							</SplideSlide>
						))}
					</Splide>
				</>
			) : (
				<ShimmerProducts option={option} columns={3} />
			)}
		</div>
	);
}
export default Collections;
